<template>
  <!-- Particles Background -->
  <vue-particles
    id="tsparticles"
    @particles-loaded="particlesLoaded"
    :options="particlesOptions"
  />

  <!-- Main Container -->
  <div class="hero-container">
    <!-- Animated Gradient Background -->
    <div class="gradient-bg"></div>

    <div class="container content-wrapper">
      <div class="row align-items-center hero-content">
        <!-- Left Content Section -->
        <div class="col-lg-7 content-left">
          <div
            class="intro-text"
            data-aos="fade-right"
            data-aos-duration="1000"
          >
            <h3 class="greeting">Hello World, I'm</h3>
            <h1 class="name">{{ userData.authorName }}</h1>

            <!-- Typewriter Effect -->
            <div class="typewriter-container">
              <span class="typewriter-prefix">I am a </span>
              <span class="typewriter-text" ref="typewriter"></span>
              <span :class="{ typing: isTyping }">|</span>
            </div>

            <p class="bio" data-aos="fade-up" data-aos-delay="200">
              {{ userData.homeMsg }}
            </p>

            <!-- CTA Buttons -->
            <div class="cta-buttons" data-aos="fade-up" data-aos-delay="400">
              <a
                :href="userData.authorPdf"
                download="Resume.pdf"
                class="btn primary-btn"
              >
                <i class="bi bi-file-earmark-arrow-down"></i> Download Resume
              </a>
              <a
                href="mailto:tawshiq.rafi02@gmail.com"
                class="btn secondary-btn"
              >
                <i class="bi bi-chat-dots"></i> Let's Connect
              </a>
            </div>

            <!-- Stats -->
            <div
              class="stats-container"
              data-aos="fade-up"
              data-aos-delay="600"
            >
              <div class="stat-item">
                <div class="stat-number">
                  <span ref="projectCounter"></span>+
                </div>
                <div class="stat-label">Projects</div>
              </div>
              <div class="stat-item">
                <div class="stat-number">
                  <span ref="experienceCounter"></span>+
                </div>
                <div class="stat-label">Years Experience</div>
              </div>
              <div class="stat-item">
                <div class="stat-number">
                  <span ref="clientsCounter"></span>+
                </div>
                <div class="stat-label">Happy Clients</div>
              </div>
            </div>
          </div>
        </div>

        <!-- Right Image Section with 3D Effect -->
        <div
          class="col-lg-5 content-right"
          data-aos="fade-left"
          data-aos-duration="1000"
        >
          <div class="profile-wrapper">
            <div class="profile-card">
              <div class="profile-img-container">
                <img
                  :src="userData.authorImg"
                  :alt="userData.authorName"
                  class="profile-img"
                />
                <div class="glow-effect"></div>
              </div>
              <div class="tech-stack">
                <div
                  v-for="(tech, index) in techStack"
                  :key="index"
                  class="tech-badge"
                  :style="{ animationDelay: `${index * 0.2}s` }"
                >
                  <i :class="tech.icon"></i>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Floating Social Media Section -->
    <div class="social-sidebar" data-aos="fade-right">
      <div class="social-icon-wrapper">
        <a
          v-for="(social, index) in socialLinks"
          :key="index"
          :href="social.link"
          class="social-icon"
          target="_blank"
          rel="noopener noreferrer"
        >
          <i :class="social.icon"></i>
          <span class="social-tooltip">{{ social.name }}</span>
        </a>
      </div>
      <div class="follow-text">Follow Me</div>
    </div>

    <!-- Scroll Indicator -->
    <div class="scroll-indicator" data-aos="fade-up" data-aos-delay="1200">
      <div class="scroll-text">Navigation in Top</div>
      <div class="scroll-icon">
        <i class="bi bi-airplane"></i>
        <div class="scroll-wheel"></div>
      </div>
    </div>
  </div>

  <!-- Footer -->
  <footer class="footer">
    <div class="container">
      <div class="copyright">
        &copy; {{ new Date().getFullYear() }} Crafted with
        <i class="bi bi-heart-fill pulse"></i> by
        <a href="mailto:tawshiq.rafi02@gmail.com">Tawshiqul Islam</a>
      </div>
    </div>
  </footer>
</template>

<script>
import homeData from "@/assets/data/home.json";
import AOS from "aos";
import "aos/dist/aos.css";

export default {
  data() {
    return {
      userData: homeData.userData,
      isTyping: true,
      typewriterPhrases: homeData.typewriterPhrases,
      currentPhraseIndex: 0,
      currentCharIndex: 0,
      typewriterSpeed: {
        type: 100, // typing speed in ms
        delete: 50, // deletion speed in ms
        pause: 2000, // pause between phrases in ms
      },
      socialLinks: [
        {
          name: "GitHub",
          icon: "bi bi-github",
          link: "https://github.com/tawshiqulislam",
        },
        {
          name: "LinkedIn",
          icon: "bi bi-linkedin",
          link: "https://www.linkedin.com/in/tawshiqul-islam-rafi/",
        },
        {
          name: "Facebook",
          icon: "bi bi-facebook",
          link: "https://www.facebook.com/tawshiqulislam.rafi.3",
        },
      ],
      techStack: [
        { icon: "bi bi-filetype-js" },
        { icon: "bi bi-filetype-css" },
        { icon: "bi bi-filetype-sql" },
        { icon: "bi bi-filetype-php" },
        { icon: "bi bi-filetype-ai" },
      ],
      particlesOptions: {
        background: { color: { value: "transparent" } },
        fullScreen: { enable: true, zIndex: -1 },
        fpsLimit: 60,
        interactivity: {
          detectsOn: "window",
          events: {
            onClick: { enable: true, mode: "push" },
            onHover: {
              enable: true,
              mode: "repulse",
              parallax: { enable: true, force: 60, smooth: 100 },
            },
            touchstart: {
              // Add this to handle touch events properly
              enable: true,
              mode: "push",
            },
            touchmove: {
              // Add this to allow scrolling while touching particles
              enable: true,
              mode: "grab",
            },
          },
          modes: {
            repulse: {
              distance: 200,
              duration: 0.4,
            },
            push: { quantity: 4 },
          },
        },
        particles: {
          color: { value: "#ffffff" },
          links: {
            color: "#ffffff",
            distance: 150,
            enable: true,
            opacity: 0.2,
            width: 1,
          },
          move: {
            direction: "none",
            enable: true,
            outModes: { default: "bounce" },
            random: false,
            speed: 2,
            straight: false,
          },
          number: { density: { enable: true, area: 800 }, value: 120 },
          opacity: { value: 0.2 },
          shape: { type: "circle" },
          size: { value: { min: 1, max: 3 } },
        },
        detectRetina: true,
      },
    };
  },
  mounted() {
    // Initialize AOS (Animate on Scroll)
    AOS.init();

    // Start the typewriter effect
    this.typeWriter();

    // Start counters animation
    this.animateCounters();

    // Add mouse move effect to profile image
    document
      .querySelector(".profile-card")
      .addEventListener("mousemove", this.profileCardTilt);
    document
      .querySelector(".profile-card")
      .addEventListener("mouseleave", this.resetTilt);
  },
  methods: {
    typeWriter() {
      const currentPhrase = this.typewriterPhrases[this.currentPhraseIndex];

      if (this.currentCharIndex < currentPhrase.length) {
        // Typing forward
        this.isTyping = true;
        if (this.$refs.typewriter) {
          this.$refs.typewriter.textContent = currentPhrase.substring(
            0,
            this.currentCharIndex + 1
          );
          this.currentCharIndex++;
          setTimeout(this.typeWriter, this.typewriterSpeed.type);
        }
      } else {
        // Pause at the end of phrase
        this.isTyping = false;
        setTimeout(() => {
          this.deleteText();
        }, this.typewriterSpeed.pause);
      }
    },
    deleteText() {
      const currentPhrase = this.typewriterPhrases[this.currentPhraseIndex];

      if (this.currentCharIndex > 0) {
        // Deleting
        this.isTyping = true;
        if (this.$refs.typewriter) {
          this.$refs.typewriter.textContent = currentPhrase.substring(
            0,
            this.currentCharIndex - 1
          );
          this.currentCharIndex--;
          setTimeout(this.deleteText, this.typewriterSpeed.delete);
        }
      } else {
        // Move to next phrase
        this.isTyping = false;
        this.currentPhraseIndex =
          (this.currentPhraseIndex + 1) % this.typewriterPhrases.length;
        setTimeout(this.typeWriter, this.typewriterSpeed.type);
      }
    },
    animateCounters() {
      // Animate project counter
      this.animateCounter(this.$refs.projectCounter, 0, 23, 2000);

      // Animate experience counter
      this.animateCounter(this.$refs.experienceCounter, 0, 3, 1500);

      // Animate clients counter
      this.animateCounter(this.$refs.clientsCounter, 0, 9, 2000);
    },
    animateCounter(element, start, end, duration) {
      let startTimestamp = null;
      const step = (timestamp) => {
        if (!startTimestamp) startTimestamp = timestamp;
        const progress = Math.min((timestamp - startTimestamp) / duration, 1);
        const value = Math.floor(progress * (end - start) + start);
        element.innerHTML = value;
        if (progress < 1) {
          window.requestAnimationFrame(step);
        }
      };
      window.requestAnimationFrame(step);
    },
    profileCardTilt(e) {
      const card = e.currentTarget;
      const rect = card.getBoundingClientRect();
      const x = e.clientX - rect.left;
      const y = e.clientY - rect.top;

      const centerX = rect.width / 2;
      const centerY = rect.height / 2;

      const rotateY = ((x - centerX) / centerX) * 10;
      const rotateX = ((centerY - y) / centerY) * 10;

      card.style.transform = `perspective(1000px) rotateX(${rotateX}deg) rotateY(${rotateY}deg)`;

      // Move glow effect
      const glowElement = card.querySelector(".glow-effect");
      glowElement.style.background = `radial-gradient(circle at ${x}px ${y}px, rgba(255, 255, 255, 0.3), transparent 40%)`;
    },
    resetTilt(e) {
      const card = e.currentTarget;
      card.style.transform = "perspective(1000px) rotateX(0deg) rotateY(0deg)";
      card.style.transition = "transform 0.5s ease";
    },
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&family=Montserrat:wght@700;800;900&display=swap");

/* Main Styles */
