.gradient-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: gradient 15s ease infinite;
  z-index: -2;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

.content-wrapper {
  position: relative;
  z-index: 1;
}

/* Hero Content */
.hero-content {
  min-height: 80vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Left Content Section */
.content-left {
  padding: 30px;
}

.greeting {
  font-size: 1.5rem;
  color: #aab8d8;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.name {
  font-family: "Montserrat", sans-serif;
  font-size: 4rem;
  font-weight: 800;
  margin-bottom: 1rem;
  background: linear-gradient(to right, #ff7b00, #ff3c78);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  text-shadow: 2px 2px 8px rgba(255, 60, 120, 0.3);
}

/* Typewriter Effect */
.typewriter-container {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
}

.typewriter-prefix {
  color: #ffffff;
}

.typewriter-text {
  color: #5cefff;
  margin-left: 8px;
}

@keyframes blink {
  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }
}

.bio {
  font-size: 1.1rem;
  line-height: 1.8;
  color: #d1d9ee;
  margin-bottom: 2rem;
  max-width: 600px;
}

/* CTA Buttons */
.cta-buttons {
  display: flex;
  gap: 20px;
  margin-bottom: 2.5rem;
}

.btn {
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 50px;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
}

.primary-btn {
  background: linear-gradient(45deg, #ff3c78, #ff7b00);
  border: none;
  color: white;
  box-shadow: 0 8px 15px rgba(255, 60, 120, 0.3);
}

.primary-btn:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 20px rgba(255, 60, 120, 0.5);
}

.secondary-btn {
  background: transparent;
  color: #fff;
  border: 2px solid #5cefff;
  box-shadow: 0 8px 15px rgba(92, 239, 255, 0.1);
}

.secondary-btn:hover {
  background: rgba(0, 152, 169, 0.653);
  transform: translateY(-5px);
  box-shadow: 0 12px 20px rgba(92, 239, 255, 0.2);
}

/* Stats Container */
.stats-container {
  display: flex;
  gap: 40px;
  margin-top: 3rem;
}

.stat-item {
  text-align: center;
}

.stat-number {
  font-size: 2.5rem;
  font-weight: 700;
  color: #5cefff;
  font-family: "Montserrat", sans-serif;
}

.stat-label {
  font-size: 1rem;
  color: #aab8d8;
}

/* Profile Card with 3D Effect */
.content-right {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-wrapper {
  position: relative;
  perspective: 1000px;
}

.profile-card {
  width: 350px;
  height: 420px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  transition: transform 0.5s ease;
  transform-style: preserve-3d;
}

.profile-img-container {
  position: relative;
  width: 80%;
  height: 80%;
  border-radius: 15px;
  overflow: hidden;
}

.profile-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 15px;
  transform: scale(1.05);
  transition: all 0.5s ease;
}

.profile-card:hover .profile-img {
  transform: scale(1.1);
}

.glow-effect {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle at 50% 50%,
    rgba(255, 255, 255, 0.2),
    transparent 40%
  );
  opacity: 0;
  transition: opacity 0.3s ease;
}

.profile-card:hover .glow-effect {
  opacity: 1;
}

/* Tech Stack Badges */
.tech-stack {
  position: absolute;
  display: flex;
  gap: 15px;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}

.tech-badge {
  width: 40px;
  height: 40px;
  background: rgba(21, 117, 186, 0.396);
  backdrop-filter: blur(5px);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  animation: float 3s ease-in-out infinite;
}

.tech-badge i {
  font-size: 1.2rem;
  color: rgb(255, 217, 0);
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

/* Social Sidebar */
.social-sidebar {
  position: fixed;
  left: 30px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 10;
}

.social-icon-wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.social-icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 1.2rem;
  transition: all 0.3s ease;
  position: relative;
}

.social-icon:hover {
  transform: translateY(-5px);
  background: linear-gradient(45deg, #ff3c78, #ff7b00);
  box-shadow: 0 8px 15px rgba(255, 60, 120, 0.3);
}

.social-tooltip {
  position: absolute;
  left: 50px;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 0.8rem;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.social-icon:hover .social-tooltip {
  opacity: 1;
  visibility: visible;
}

.follow-text {
  margin-top: 50px;
  transform: rotate(-90deg);
  white-space: nowrap;
  color: #aab8d8;
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
}

/* Scroll Indicator */
.scroll-indicator {
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #aab8d8;
}

.scroll-text {
  font-size: 0.9rem;
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.scroll-icon {
  position: relative;
  font-size: 1.5rem;
}

.scroll-wheel {
  position: absolute;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
  width: 4.5px;
  height: 20.5px;
  background-color: #aab8d8;
  border-radius: 10px;
  animation: scrollWheel 1.5s infinite;
}

@keyframes scrollWheel {
  0% {
    transform: translateX(-50%) translateY(-5px);
    opacity: 0;
  }

  50% {
    transform: translateX(-50%) translateY(0);
    opacity: 1;
  }

  100% {
    transform: translateX(-50%) translateY(5px);
    opacity: 0;
  }
}

/* Footer */
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: rgba(10, 16, 30, 0.8);
  backdrop-filter: blur(10px);
  padding: 15px 0;
  text-align: center;
  z-index: 5;
}

.copyright {
  font-size: 0.9rem;
  color: #aab8d8;
}

.copyright a {
  color: #5cefff;
  text-decoration: none;
  transition: color 0.3s ease;
}

.copyright a:hover {
  color: #ff3c78;
}

.bi-heart-fill {
  color: #ff3c78;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

/* Responsive Design */
@media (max-width: 1200px) {
  .name {
    font-size: 3.5rem;
  }
}

@media (max-width: 992px) {
  .hero-content {
    flex-direction: column;
    text-align: center;
  }

  .content-left {
    order: 2;
    padding-top: 50px;
  }

  .content-right {
    order: 1;
    margin-bottom: 30px;
  }

  .bio {
    max-width: 100%;
  }

  .cta-buttons {
    justify-content: center;
  }

  .stats-container {
    justify-content: center;
  }

  .social-sidebar {
    position: relative;
    left: 0;
    top: auto;
    transform: none;
  }

  .social-icon-wrapper {
    flex-direction: row;
    gap: 15px;
    margin-top: -10px;
  }

  .follow-text {
    display: none;
  }
}

@media (max-width: 768px) {
