  }

  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }

  .btn {
    width: 100%;
    justify-content: center;
  }

  .stats-container {
    flex-wrap: wrap;
    justify-content: center;
  }

  .profile-card {
    width: 280px;
    height: 350px;
  }
}

@media (max-width: 576px) {
  .greeting {
    font-size: 1.2rem;
  }

  .name {
    font-size: 2.5rem;
  }

  .typewriter-container {
    font-size: 1.3rem;
  }

  .bio {
    font-size: 1rem;
  }

  .social-sidebar {
    display: none;
  }
}
</style>
