<template>
  <header class="navbar-wrapper" :class="{ 'navbar-scrolled': hasScrolled }">
    <div class="navbar-container">
      <!-- Logo Section -->
      <router-link to="/" class="nav-logo" @click="toggleBack">
        <div class="logo-container" :class="{ 'back-mode': isAnyNavActive }">
          <transition name="logo-flip">
            <img
              v-if="!isAnyNavActive"
              :src="logoSrc"
              alt="Logo"
              class="logo-image"
            />
            <!-- <span v-if="!isAnyNavActive" class="logo-image">&reg;</span> -->
            <div v-else class="back-button">
              <i class="bi bi-arrow-left-circle-fill h1 text-warning"
                >&emsp;Home</i
              >
            </div>
          </transition>
        </div>
      </router-link>

      <!-- Desktop Menu -->
      <nav class="desktop-menu" :class="{ 'menu-hidden': isMobileMenuOpen }">
        <ul class="nav-list">
          <li v-for="(item, index) in navItems" :key="index" class="nav-item">
            <router-link
              :to="item.path"
              class="nav-link"
              :class="{ 'nav-active': toggleNav[index] === 1 }"
              @click="toggleNavItem(index)"
            >
              <div class="nav-icon-wrapper">
                <i :class="'bi ' + item.icon"></i>
                <div
                  class="nav-pulse"
                  :class="{ 'pulse-active': toggleNav[index] === 1 }"
                ></div>
              </div>
              <span class="nav-text">{{ item.name }}</span>
              <div class="nav-indicator"></div>
            </router-link>
          </li>
        </ul>
      </nav>

      <!-- Mobile Menu Toggle Button -->
      <button
        class="mobile-toggle"
        @click="toggleMobileMenu"
        aria-label="Toggle mobile menu"
      >
        <div class="hamburger" :class="{ 'is-active': isMobileMenuOpen }">
          <span>&#8737;</span>
        </div>
      </button>
    </div>

    <!-- Mobile Menu Overlay -->
    <transition name="mobile-menu">
      <div
        v-if="isMobileMenuOpen"
        class="mobile-menu-overlay"
        @click="handleOverlayClick"
      >
        <div class="mobile-menu-container" ref="mobileMenuRef">
          <ul class="mobile-nav-list">
            <li
              v-for="(item, index) in navItems"
              :key="index"
              class="mobile-nav-item"
              :style="{ animationDelay: index * 0.1 + 's' }"
            >
              <router-link
                :to="item.path"
                class="mobile-nav-link"
                :class="{ 'mobile-nav-active': toggleNav[index] === 1 }"
                @click="mobileNavClick(index)"
              >
                <i :class="'bi ' + item.icon"></i>
                <span>{{ item.name }}</span>
                <div class="mobile-link-highlight"></div>
              </router-link>
            </li>
          </ul>
          <div class="mobile-menu-footer">
            <div class="social-links">
              <a
                href="https://github.com/tawshiqulislam"
                class="social-link"
                target="_blank"
                aria-label="GitHub"
              >
                <i class="bi bi-github"></i>
              </a>
              <a
                href="https://www.linkedin.com/in/tawshiqul-islam-rafi/"
                class="social-link"
                target="_blank"
                aria-label="LinkedIn"
              >
                <i class="bi bi-linkedin"></i>
              </a>
              <a
                href="https://www.facebook.com/tawshiqulislam.rafi.3"
                class="social-link"
                target="_blank"
                aria-label="facebook"
              >
                <i class="bi bi-facebook"></i>
              </a>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </header>
  <router-view />
</template>

<script>
import logo from "./assets/image/logo.png";

export default {
  data() {
    return {
      logoSrc: logo,
      toggleNav: [0, 0, 0, 0, 0],
      isMobileMenuOpen: false,
      hasScrolled: false,
      navItems: [
        { name: "About", icon: "bi-person-circle", path: "/about" },
        { name: "Services", icon: "bi-box2-heart-fill", path: "/services" },
        { name: "Expertise", icon: "bi-briefcase-fill", path: "/expertise" },
        { name: "Projects", icon: "bi-bar-chart-steps", path: "/projects" },
        { name: "Blog", icon: "bi-journal-text", path: "/blogs" },
        { name: "Contact", icon: "bi-question-circle-fill", path: "/contact" },
      ],
    };
  },
  computed: {
    isAnyNavActive() {
      return this.toggleNav.includes(1);
    },
  },
  mounted() {
    // Add scroll event listener
    window.addEventListener("scroll", this.handleScroll);

    // Check if we're on a specific route when the component is mounted
    this.checkCurrentRoute();

    // Add resize listener for animations
    window.addEventListener("resize", this.checkScreenSize);
    this.checkScreenSize();
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
    window.removeEventListener("resize", this.checkScreenSize);
  },
  methods: {
    checkCurrentRoute() {
      const currentPath = this.$route.path;
      const index = this.navItems.findIndex(
        (item) => item.path === currentPath
      );
      if (index !== -1) {
        this.toggleNavItem(index);
      }
    },
    toggleNavItem(index) {
      const newToggleNav = [0, 0, 0, 0, 0];
      newToggleNav[index] = 1;
      this.toggleNav = newToggleNav;
      if (this.isMobileMenuOpen) {
        setTimeout(() => {
          this.isMobileMenuOpen = false;
        }, 300);
      }
    },
    toggleBack() {
      this.toggleNav = [0, 0, 0, 0, 0];
    },
    handleOverlayClick(event) {
      if (event.target === event.currentTarget) {
        this.closeMobileMenu();
      }
    },
    closeMobileMenu() {
      this.isMobileMenuOpen = false;
      document.body.style.overflow = "";
    },
    toggleMobileMenu() {
      this.isMobileMenuOpen = !this.isMobileMenuOpen;
      document.body.style.overflow = this.isMobileMenuOpen ? "hidden" : "";
    },
    beforeRouteLeave(to, from, next) {
      this.closeMobileMenu();
      next();
    },
    mobileNavClick(index) {
      this.toggleNavItem(index);
    },
    handleScroll() {
      this.hasScrolled = window.scrollY > 20;
    },
    checkScreenSize() {
      if (window.innerWidth > 768) {
        this.isMobileMenuOpen = false;
        document.body.style.overflow = "";
      }
    },
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

/* Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-color: #ffbf00;
  --primary-hover: #ffcc33;
  --dark-bg: #111111;
  --light-text: #ffffff;
  --gray-text: #a0a0a0;
  --navbar-bg: rgba(17, 17, 17, 0.8);
  --navbar-scrolled-bg: rgba(17, 17, 17, 0.95);
  --transition-speed: 0.3s;
  --cubic-bezier: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

/* Navbar Wrapper */
.navbar-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  padding: 15px 0;
  background: var(--navbar-bg);
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.navbar-scrolled {
  padding: 10px 0;
  background: var(--navbar-scrolled-bg);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.navbar-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Logo Styles */
.nav-logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  z-index: 1001;
}

.logo-container {
  position: relative;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s var(--cubic-bezier);
}

.logo-container.back-mode {
  width: auto;
}

.logo-image {
  width: 40px;
  height: 40px;
  object-fit: contain;
  transition: transform 0.3s var(--cubic-bezier);
  font-size: 2rem;
  color: orange;
}

.logo-container:hover .logo-image {
  transform: scale(1.1);
}

.back-button {
  display: flex;
  align-items: center;
  padding: 8px 16px;
  background: linear-gradient(
    135deg,
    var(--primary-color),
    var(--primary-hover)
  );
  border-radius: 20px;
  color: var(--dark-bg);
  font-weight: 600;
  font-size: 14px;
  transition: all 0.3s ease;
}

.back-button i {
  margin-right: 8px;
  font-size: 16px;
}

.back-button:hover {
  transform: translateX(-5px);
  box-shadow: 0 5px 15px rgba(255, 191, 0, 0.3);
}

/* Desktop Menu Styles */
.desktop-menu {
  display: block;
}

.nav-list {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 30px;
  padding: 5px;
  backdrop-filter: blur(5px);
}

.nav-item {
  position: relative;
  margin: 0 2px;
}

.nav-link {
  position: relative;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: var(--gray-text);
  padding: 8px 16px;
  font-size: 16px;
  font-weight: 500;
  transition: color 0.3s ease;
  border-radius: 25px;
  overflow: hidden;
}

.nav-link:hover {
  color: var(--light-text);
}

.nav-active {
  background: linear-gradient(
    135deg,
    rgba(255, 191, 0, 0.2),
    rgba(255, 204, 51, 0.1)
  );
  color: var(--primary-color);
}

.nav-icon-wrapper {
  position: relative;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
}

.nav-pulse {
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: var(--primary-color);
  opacity: 0;
  transform: scale(0);
  transition: all 0.3s ease;
  top: -2px;
  right: -2px;
}

.pulse-active {
  opacity: 1;
  transform: scale(1);
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 191, 0, 0.7);
  }

  70% {
    box-shadow: 0 0 0 10px rgba(255, 191, 0, 0);
  }

  100% {
    box-shadow: 0 0 0 0 rgba(255, 191, 0, 0);
  }
}

.nav-text {
  position: relative;
  z-index: 1;
  transition: all 0.3s ease;
}

.nav-indicator {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background: linear-gradient(
    to right,
    var(--primary-color),
    var(--primary-hover)
  );
  transition: all 0.3s ease;
  transform: translateX(-50%);
  opacity: 0;
}

.nav-link:hover .nav-indicator {
  width: 40%;
  opacity: 1;
}

.nav-active .nav-indicator {
  width: 60%;
  opacity: 1;
}

/* Mobile Toggle Button */
.mobile-toggle {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  z-index: 1001;
}

.hamburger {
  width: 30px;
  height: 20px;
  position: relative;
  transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

.hamburger span {
  font-size: 42px;
  color: orange;
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background: var(--light-text);
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

.hamburger span:nth-child(1) {
  top: -20px;
  left: -20px;
}

.hamburger.is-active span:nth-child(1) {
  top: 10px;
  transform: rotate(90deg);
  background: var(--primary-color);
}

/* Mobile Menu Overlay */
.mobile-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(32, 177, 230, 0.879);
  backdrop-filter: blur(10px);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
}

.mobile-menu-container {
  width: 100%;
  max-width: 400px;
  padding: 30px;
  position: relative;
  box-shadow: rgb(0 255 104 / 46%) 0px 2px 4px 0px,
    rgb(255 209 0 / 54%) 0px 2px 16px 0px;
  border-radius: 10px;
  background: var(--dark-bg);
  color: var(--light-text);
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  transition: all 0.3s ease;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  -webkit-backdrop-filter: blur(10px);
}

.mobile-menu-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.mobile-menu-title {
  color: var(--light-text);
  font-size: 24px;
  font-weight: 600;
}

.mobile-close {
  background: none;
  border: none;
  color: var(--light-text);
  font-size: 24px;
  cursor: pointer;
  padding: 5px;
  transition: all 0.3s ease;
}

.mobile-close:hover {
  color: var(--primary-color);
  transform: rotate(90deg);
}

.mobile-nav-list {
  list-style-type: none;
  padding: 0;
}

.mobile-nav-item {
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.5s forwards;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.mobile-nav-link {
  color: azure !important;
  display: flex;
  align-items: center;
  padding: 15px 0;
  text-decoration: none;
  color: var(--gray-text);
  font-size: 18px;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.mobile-nav-link i {
  margin-right: 15px;
  width: 24px;
  text-align: center;
  font-size: 20px;
  transition: all 0.3s ease;
}

.mobile-link-highlight {
  position: absolute;
  left: -5px;
  top: 50%;
  transform: translateY(-50%);
  width: 3px;
  height: 0;
  background: var(--primary-color);
  transition: all 0.3s ease;
  opacity: 0;
}

.mobile-nav-link:hover {
  transform: translateY(-5px);
  background: linear-gradient(45deg, #ff3c78, #ff7b00);
  box-shadow: 0 8px 15px rgba(255, 60, 120, 0.3);
  padding-left: 15px;
}

.mobile-nav-link:hover .mobile-link-highlight {
  height: 70%;
  opacity: 1;
}

.mobile-nav-link:hover i {
  color: var(--primary-color);
}

.mobile-nav-active {
  color: var(--primary-color);
}

.mobile-nav-active .mobile-link-highlight {
  height: 70%;
  opacity: 1;
}

.mobile-menu-footer {
  margin-top: 40px;
  display: flex;
  justify-content: center;
}

.social-links {
  display: flex;
  gap: 15px;
}

.social-link {
  color: #ff7b00;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 50%;
  color: var(--light-text);
  font-size: 18px;
  transition: all 0.3s ease;
}

.social-link i {
  color: azure;
}

.social-link:hover {
  transform: translateY(-5px);
  background: linear-gradient(45deg, #ff3c78, #ff7b00);
  box-shadow: 0 8px 15px rgba(255, 60, 120, 0.3);
}

/* Transitions */
.logo-flip-enter-active,
.logo-flip-leave-active {
  transition: all 0.5s var(--cubic-bezier);
  position: absolute;
}

.logo-flip-enter-from,
.logo-flip-leave-to {
  opacity: 0;
  transform: rotateY(90deg);
}

.mobile-menu-enter-active,
.mobile-menu-leave-active {
  transition: all 0.3s ease;
}

.mobile-menu-enter-from,
.mobile-menu-leave-to {
  opacity: 0;
  transform: scale(1.1);
}

/* Responsive Styles */
@media (max-width: 768px) {
  .desktop-menu {
    display: none;
  }

  .mobile-toggle {
    display: block;
  }

  .nav-logo {
    flex-grow: 1;
  }
}

@media (min-width: 769px) {
  .mobile-menu-overlay {
    display: none !important;
  }
}

@media (max-width: 576px) {
  .navbar-container {
    padding: 0 15px;
  }

  .logo-container {
    width: 40px;
    height: 40px;
  }

  .logo-image {
    width: 35px;
    height: 35px;
  }

  .mobile-menu-container {
    padding: 20px;
  }
}
</style>
